.services {
  padding: 0px;

  .topSection {
    text-align: center;
    /* margin-bottom: 40px; */
    height: 600px;
    display: flex;
    align-items: center;
    padding: 20px;

    @media (max-width: 1200px) {
      height: 550px;
      padding: 15px;
    }

    @media (max-width: 992px) {
      height: 500px;
      padding: 12px;
    }

    @media (max-width: 768px) {
      height: 450px;
      padding: 10px;
    }

    @media (max-width: 576px) {
      height: 400px;
      padding: 8px;
    }

    @media (max-width: 400px) {
      height: 350px;
      padding: 5px;
    }

    .heroimg {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: -1;
      max-height: 600px;

      @media (max-width: 1200px) {
        max-height: 550px;
      }

      @media (max-width: 992px) {
        max-height: 500px;
      }

      @media (max-width: 768px) {
        max-height: 450px;
      }

      @media (max-width: 576px) {
        max-height: 400px;
      }

      @media (max-width: 400px) {
        max-height: 350px;
      }
    }

    div {
      margin: auto;
      h1 {
        font-weight: bold;
        font-family: bwvirat;
        margin: 20px;
        font-size: 64px;
        color: white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
        letter-spacing: 1px;

        @media (max-width: 1200px) {
          font-size: 56px;
          margin: 15px;
        }

        @media (max-width: 992px) {
          font-size: 48px;
          margin: 12px;
          letter-spacing: 0.8px;
        }

        @media (max-width: 768px) {
          font-size: 36px;
          letter-spacing: 0.5px;
          margin: 10px;
        }

        @media (max-width: 576px) {
          font-size: 38px;
          letter-spacing: 0.3px;
          margin: 8px;
        }

        @media (max-width: 400px) {
          font-size: 38px;
          letter-spacing: 0.2px;
          margin: 5px;
        }
      }

      p {
        font-family: virat;
        font-size: 22px;
        line-height: 1.8;
        color: #ffffff;
        width: 60%;
        margin: 0 auto;
        text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
        font-weight: 400;
        letter-spacing: 0.3px;
        text-align: center;

        @media (max-width: 1200px) {
          font-size: 20px;
          width: 65%;
          line-height: 1.7;
        }

        @media (max-width: 992px) {
          font-size: 19px;
          width: 70%;
          line-height: 1.6;
          letter-spacing: 0.25px;
        }

        @media (max-width: 768px) {
          font-size: 18px;
          width: 85%;
          line-height: 1.6;
          letter-spacing: 0.2px;
        }

        @media (max-width: 576px) {
          font-size: 16px;
          width: 90%;
          line-height: 1.5;
          letter-spacing: 0.15px;
        }

        @media (max-width: 400px) {
          font-size: 14px;
          width: 95%;
          line-height: 1.4;
          letter-spacing: 0.1px;
        }
      }
    }
  }
}

.servicesSection {
  position: relative;
  padding: 50px 20px;
  color: #fff;
  text-align: center;
  overflow: hidden;

  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../assets/Hero\ Section.webp");
    background-size: cover;
    background-position: center;
    z-index: -1;
  }

  .textContainer {
    padding-left: 5%;
    margin-bottom: 30px;
    text-align: left;
    div {
      display: flex;
      flex-direction: row;
      gap: 20px;
    }
    h2 {
      font-family: bwvirat;
      font-size: 24px;
      font-weight: bold;
      margin: 0;
    }

    h1 {
      font-family: bwvirat;
      font-size: 64px;
      font-weight: bold;
      margin: 10px 0;

      @media (max-width: 768px) {
        font-size: 36px;
      }
    }

    p {
      font-size: 18px;
      font-family: virat;

      @media (max-width: 768px) {
        font-size: 16px;
      }
    }
  }
  .cardsContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;

    @media (max-width: 768px) {
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
      gap: 15px;
      padding: 0 10px;
      justify-content: flex-start;
      scroll-behavior: smooth;
      -webkit-overflow-scrolling: touch;
    }

    .card {
      background: #fff;
      color: #333;
      border-radius: 10px;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
      overflow: hidden;
      width: 424px;
      height: 456px;
      text-align: left;
      position: relative;
      flex-shrink: 0;

      @media (max-width: 768px) {
        width: 280px;
        height: 320px;
        flex-shrink: 0;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .cardText {
        width: 100%;
        position: absolute;
        bottom: 0;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #1c2325;
        background-color: white;

        h3 {
          font-size: 18px;
          margin: 0;
          font-weight: bold;
        }

        img {
          width: 20%; /* Adjust size of the icon */
          height: 40px;
          flex-shrink: 0; /* Prevent icon from shrinking */
        }

        .icon {
          font-size: 20px;
        }
      }
    }

  }
}

.contactSection {
  position: relative;
  width: 100%;
  height: 400px;

  @media (max-width: 768px) {
    height: 600px;
  }

  .mapContainer {
    width: 100%;
    height: 100%;
    iframe {
      width: 100%;
      height: 100%;
      border: 0;
    }
  }

  .contactCard {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 20px;
    width: 80%;
    max-width: 900px;
    display: flex;
    justify-content: space-around;
    align-items: center;

    @media (max-width: 768px) {
      flex-direction: column;
      padding: 10px;
    }

    .contactItem {
      display: flex;
      align-items: center;
      margin: 10px 20px;

      @media (max-width: 768px) {
        flex-direction: column;
        text-align: center;
      }

      .icon {
        font-size: 24px;
        margin-right: 10px;

        @media (max-width: 768px) {
          margin-right: 0;
          margin-bottom: 5px;
        }
      }

      h3 {
        font-size: 18px;
        font-weight: bold;
      }

      p {
        font-size: 16px;
        color: #555;
      }
    }
  }
}
